import React, { useRef, useMemo } from 'react';
import { Points, PointMaterial } from '@react-three/drei';
import * as THREE from 'three';
import { useFrame } from '@react-three/fiber';

const SnowParticles = ({ count = 1000, radius = 2 }) => {
    const pointsRef = useRef();

    //     
    const particles = useMemo(() => {
        const positions = new Float32Array(count * 3);
        for (let i = 0; i < count; i++) {
            const angle = Math.random() * Math.PI * 2; //  
            const distance = Math.sqrt(Math.random()) * radius; //  
            positions[i * 3] = Math.cos(angle) * distance; // X
            positions[i * 3 + 1] = Math.random() * radius * 2; // Y
            positions[i * 3 + 2] = Math.sin(angle) * distance; // Z
        }
        return positions;
    }, [count, radius]);

    //   ( )
    useFrame((state, delta) => {
        if (pointsRef.current) {
            pointsRef.current.rotation.y += delta * 0.1; //  
            const positions = pointsRef.current.geometry.attributes.position.array;
            for (let i = 1; i < positions.length; i += 3) {
                positions[i] -= delta * 0.5; //  
                if (positions[i] < -radius * 2) {
                    positions[i] = radius * 2; //   ,     
                }
            }
            pointsRef.current.geometry.attributes.position.needsUpdate = true;
        }
    });

    return (
        <Points ref={pointsRef} positions={particles}>
            <PointMaterial
                transparent
                color="white"
                size={0.05}
                sizeAttenuation={true}
                alphaTest={0.5}
                opacity={0.8}
            />
        </Points>
    );
};

export default SnowParticles;